@use "../helpers/variables" as *;
@use "../helpers/mixins" as *;

.screen {
	overflow-y: auto;
	position: fixed;
	width: 100dvw;
	height: 100dvh;
	left: 100dvw;
	top: 0;
	display: none;
	transition: left $animation-duration;
	> .header {
		position: sticky;
		top: 0;
		z-index: 10000;
		padding: 10px;
		margin-bottom: 10px;
		display: flex;
		align-items: center;
		> .back + span {
			text-align: center;
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			@include mobile-screens {
				font-size: 15px;
			}
		}
	}
}
